body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; 
  background: -webkit-radial-gradient(#67b26f, #4ca2cd); 
  background: -o-radial-gradient(#67b26f, #4ca2cd); 
  background: radial-gradient(#67b26f, #4ca2cd);
}

.cell {
  width: 10vw;
  height: 10vw;
  background-color: blue;
  border: solid black 2.3px;
  border-radius: 10%;
}

.blue {
  background-color: blue;
}

.red {
  background-color: red;
}

table {
  margin: auto;
}

.container {
  background-color: aquamarine;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.container div {
  padding-top: 10px;
  border: solid lightseagreen 1px;
  height: 75%;
}

.container > div {
  -ms-flex: 1 1;
      flex: 1 1;
}

.logout {
  margin-left: auto;
}


